module.exports = "## 依赖技术 \r\n\r\n* [dojo](https://dojotoolkit.org/api/)\r\n* [Vue](https://cn.vuejs.org/v2/guide/)\r\n\r\n## 一个最简单的页面\r\n\r\n1. SimpleView.js\r\n\r\n```javascript\ndefine([\r\n    \"dojo/_base/declare\",\r\n    \"desktop/application/ViewBase\",\r\n    //dojo/text! 后面是表示文件名，通常页面 js 和 html 在同一目录下\r\n    \"dojo/text!./SimpleView.html\"\r\n], function (declare, ViewBase, template) {\r\n    //declare 第一个参数表示继承，可以为 null 或 数组\r\n    return declare([ViewBase], {\r\n        title: \"一个最简单的页面\",\r\n        templateString: template,\r\n        viewName: \"SimpleView\"\r\n    })\r\n})\n```\r\n\r\n2. SimpleView.html\r\n\r\n```html\n<div>\r\n    一个最简单的页面\r\n    <p>可以写任何 html 内容</p>\r\n    <input value=\"输入框\" />\r\n    <button>按钮</button>\r\n    <p>有 data-dojo-type 属性的都是 dojo 组件</p>\r\n    <button data-dojo-type=\"dijit/form/Button\"></button>\r\n    <input data-dojo-type=\"dijit/form/TextBox\"/>\r\n    <input data-dojo-type=\"dijit/form/NumberTextBox\"/>\r\n</div>\n```\r\n\r\n3. 在 `desktop/config.json` views 属性下添加配置 **区分大小写**\r\n\r\n\r\n```json\r\n\"SimpleView\": {\r\n    \"type\": \"desktop/views/SimpleView\"\r\n}\r\n\r\n```\r\n4. 打开页面 `selectSubView` 在 `desktop/application/TabViewMixin` 中定义\r\n\r\n```javascript\r\ntopView.selectSubView(\"SimpleView\", {}, \"一个最简单的页面\");\r\n```"

